import { Input } from "@twilio-paste/core/input";
import { Label } from "@twilio-paste/core/label";
import { Box } from "@twilio-paste/core/box";
import { TextArea } from "@twilio-paste/core/textarea";
import { FormEvent, useEffect } from "react";
import { Button } from "@twilio-paste/core/button";
import { useDispatch, useSelector } from "react-redux";
import { Text } from "@twilio-paste/core/text";

import { sessionDataHandler } from "../sessionDataHandler";
import { addNotification, changeEngagementPhase, updatePreEngagementData, setErrorState } from "../store/actions/genericActions";
import { initSession } from "../store/actions/initActions";
import { AppState, EngagementPhase } from "../store/definitions";
import { Header } from "./Header";
import { notifications } from "../notifications";
import { NotificationBar } from "./NotificationBar";
import { introStyles, fieldStyles, titleStyles, formStyles } from "./styles/PreEngagementFormPhase.styles";

export const PreEngagementFormPhase = () => {
    const { name, email, query, phoneNumber } = useSelector((state: AppState) => state.session.preEngagementData) || {};
    const preengagementFormConfig = useSelector((state: AppState) => state?.config?.configOverrides?.preengagementForm);
    const welcomeMessage = useSelector((state: AppState) => state?.config?.configOverrides?.welcomeMessage);
    const widgetErrorCode = useSelector((state: AppState) => state?.session?.errorCode);
    const routingConfig = useSelector((state: AppState) => state?.config?.configOverrides?.routing);

    const dispatch = useDispatch();


   

    const handleSubmit = async (e?: FormEvent) => {
        e?.preventDefault();
        dispatch(changeEngagementPhase({ phase: EngagementPhase.Loading }));
        try {

           

            const data = await sessionDataHandler.fetchAndStoreNewSession({
                formData: {
                    name,
                    friendlyName: name,
                    email,
                    query,
                    phoneNumber,
                    welcomeMessage,
                    routing:routingConfig,
                    sourceContextPath: window.location.href
                }
            });
            dispatch(setErrorState({errorCode:undefined}));
            dispatch(initSession({ token: data.token, conversationSid: data.conversationSid }));
        } catch (err) {            
            dispatch(setErrorState({errorCode:"408"}));
            dispatch(addNotification(notifications.failedToInitSessionNotification((err as Error).message)));
            dispatch(changeEngagementPhase({ phase: EngagementPhase.PreEngagementForm }));
        }
    };

    useEffect(()=>{        
        if(preengagementFormConfig?.isEnabled===false && widgetErrorCode===undefined){          
                handleSubmit();           
        }

    },[preengagementFormConfig?.isEnabled,widgetErrorCode])

    const handleKeyPress = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
        if (e.key === "Enter" && !e.shiftKey) {
            e.preventDefault();
            handleSubmit(e);
        }
    };

    return (
        <>
            <Header />
            <NotificationBar />
            { preengagementFormConfig?.isEnabled!==false   &&
            <Box as="form" data-test="pre-engagement-chat-form" onSubmit={handleSubmit} {...formStyles}>
                <Text {...titleStyles} as="h3">
                   {preengagementFormConfig?.titleText || "Hi there!"}
                </Text>
                <Text {...introStyles} as="p">
                {preengagementFormConfig?.subtitleText || "We're here to help. Please give us some info to get started."}
                </Text>


                {
                   (preengagementFormConfig?.hiddenFields || []).indexOf('name')===-1 &&

                <Box {...fieldStyles}>
                    <Label htmlFor="name">   {preengagementFormConfig?.formFieldLabels?.name || 'Name'}</Label>
                    <Input
                        type="text"
                        aria-describedby = "name_error_help_tex"
                        id = "name"
                        placeholder={preengagementFormConfig?.formFieldLabels?.namePlaceholder || "Please enter your name"}
                        name="name"
                        data-test="pre-engagement-chat-form-name-input"
                        value={name}
                        onChange={(e) => dispatch(updatePreEngagementData({ name: e.target.value }))}
                        required
                    />
                </Box>
                }

                {
                   (preengagementFormConfig?.hiddenFields || []).indexOf('email')===-1 &&

                <Box {...fieldStyles}>
                    <Label htmlFor="email">{preengagementFormConfig?.formFieldLabels?.email || 'Email Address'}</Label>
                    <Input
                        type="email"
                        placeholder={preengagementFormConfig?.formFieldLabels?.emailPlaceholder || "Please enter your email address"}
                        name="email"
                        data-test="pre-engagement-chat-form-email-input"
                        value={email}
                        onChange={(e) => dispatch(updatePreEngagementData({ email: e.target.value }))}
                        required
                    />
                </Box>
            }

                {
                   (preengagementFormConfig?.hiddenFields || []).indexOf('phoneNumber')===-1 &&
                
                <Box {...fieldStyles}>
                    <Label htmlFor="phoneNumber">{preengagementFormConfig?.formFieldLabels?.phoneNumber || 'Phone Number'}</Label>
                    <Input
                        type="text"
                        placeholder={preengagementFormConfig?.formFieldLabels?.phoneNumberPlaceholder || "Please enter your email address"}
                        name="phoneNumber"
                        data-test="pre-engagement-chat-form-phoneNumber-input"
                        value={phoneNumber}
                        onChange={(e) => dispatch(updatePreEngagementData({ phoneNumber: e.target.value }))}
                        required
                    />
                </Box>
                }

{
                   (preengagementFormConfig?.hiddenFields || []).indexOf('query')===-1 &&

                <Box {...fieldStyles}>
                    <Label htmlFor="query">{preengagementFormConfig?.formFieldLabels?.query || 'How can we help?'}</Label>
                    <TextArea
                        placeholder={preengagementFormConfig?.formFieldLabels?.queryPlaceholder || "Ask a question"}
                        name="query"
                        data-test="pre-engagement-chat-form-query-textarea"
                        value={query}
                        onChange={(e) => dispatch(updatePreEngagementData({ query: e.target.value }))}
                        onKeyPress={handleKeyPress}
                        required
                    />
                </Box>
}

                <Button variant="primary" type="submit" data-test="pre-engagement-start-chat-button">
                    {preengagementFormConfig?.startChatText || "Start chat"}
                </Button>
            </Box>
            }   
        </>
    );
};
