import { Box } from "@twilio-paste/core/box";
import {Flex} from '@twilio-paste/core/flex';
import { Text } from "@twilio-paste/core/text";
import { useSelector } from "react-redux";
import { AppState } from "../store/definitions";
import { containerStyles, imageStyles, imageContainerStyles, titleStyles, descStyles } from "./styles/ConciergeBanner.styles";

export const ConciergeBanner = () => {
    const conciergeConfig = useSelector((state: AppState) => state?.config?.configOverrides?.concierge);

    return (
        <Box {...containerStyles}>
        <Flex >

<Flex>
    <Box {...imageContainerStyles}>
      <img alt="" src={conciergeConfig?.avatarUrl} style={{...imageStyles}} />

     </Box> 
    </Flex>

    <Flex vertical>
    <Text as="h2" {...titleStyles}>
                { conciergeConfig?.title}
            </Text>
            <Text as="p" {...descStyles}>
            { conciergeConfig?.byline}
            </Text>
    </Flex>
          
        </Flex>
        </Box>
    );
};
