import { Box } from "@twilio-paste/core/box";
import { ChatIcon } from "@twilio-paste/icons/esm/ChatIcon";
import { ChevronDownIcon } from "@twilio-paste/icons/esm/ChevronDownIcon";
import { useDispatch, useSelector } from "react-redux";
import { Text } from "@twilio-paste/core/text";

import { changeExpandedStatus } from "../store/actions/genericActions";
import { AppState } from "../store/definitions";
import { containerStyles, labelStyles  } from "./styles/EntryPoint.styles";
import { useEffect } from "react";
import { useTranslation } from "react-i18next";

export const EntryPoint = () => {
    const {i18n} = useTranslation();
    const dispatch = useDispatch();
    const entrypointConfig = useSelector((state: AppState) => state?.config?.configOverrides?.entrypoint);
    const locale = useSelector((state: AppState) => state?.config?.configOverrides?.routing?.language);
    const expanded = useSelector((state: AppState) => state.session.expanded);

    useEffect(()=>{
        if(locale!==null){
            i18n.changeLanguage(locale);
        }
    },[locale])

    return (
        <Box
            as="button"
            data-test="entry-point-button"
            onClick={() => dispatch(changeExpandedStatus({ expanded: !expanded }))}
            {...containerStyles}
        >
            {expanded ? (
                <ChevronDownIcon decorative={false} title="Minimize chat" size="sizeIcon80" />
            ) : (
                <ChatIcon decorative={false} title="Open chat" size="sizeIcon60" />
            )}

{
                (entrypointConfig?.openText && !expanded) &&
                <Text as="span" {...labelStyles}>{entrypointConfig?.openText}</Text>
            }

            {
                (entrypointConfig?.closeText && expanded) &&
                <Text as="span" {...labelStyles}>{entrypointConfig?.closeText}</Text>
            }        
        </Box>
    );
};
